import { createContext, useCallback, useContext, useEffect, useMemo, useState } from 'react'
import PropTypes from 'prop-types'
import { xgcd } from 'mathjs'
const AppContext = createContext({ })

function mod(n, m) {
  return ((n % m) + m) % m;
}

export const useApp = () => useContext(AppContext)

export const AppContextProvider = ({ children }) => {
  const [generator, setGenerator] = useState(null)
  const [params, setParams] = useState({ a: 2, b: 3 })
  const [modulus, setModulus] = useState(11)

  useEffect(() => {
    setGenerator(null)
    setParams({
      a: params.a % modulus,
      b: params.b % modulus,
    })
  }, [modulus, params.a, params.b])

  const scalars = useMemo(() => [...Array(modulus).keys()], [modulus])

  const check = useCallback(({ x, y }) => {
    return (x**3 + params.a * x + params.b - y ** 2) % modulus === 0
  }, [params.a, params.b, scalars])

  const graph = useMemo(() => scalars.reduce((allCells, col) => [
    ...allCells,
    ...scalars.map(row => ({ x: col, y: row })).filter(check)
  ], []), [params.a, params.b, scalars])

  const modInverse = useCallback(a => {
    return xgcd(a, modulus)._data[1]
  }, [modulus])
   
  const slopeAt = useCallback(p => {
    if (!p || !check(p)) { return null }
    const num = (3 * p.x ** 2 + params.a)
    const den = (2 * p.y)
    return (num * modInverse(den))
  }, [generator])

  const slopeBetween = useCallback((p, q) => {
    if (!p || !q) { return null }
    if (p.x !== q.x) {
      return ((p.y - q.y) * modInverse(p.x - q.x)) % modulus
    }
    if (p.x === q.x && p.y !== q.y) {
      return Infinity
    }
    return slopeAt(p)
  }, [generator])

  const addPoints = (p, q) => {
    // ensure points exist and are indeed on the curve
    if (!p || !q || !check(p) || !check(q)) {
      return null
    }
    const m = mod(slopeBetween(p, q), modulus)
    const x = mod((m**2 - p.x - q.x), modulus)
    const y = mod((m * (p.x - x) - p.y), modulus)
    // console.log({ m, x, y: y })
    let sum = { x: x, y: y }
    return sum
  }

  const decrement = param => setParams({ ...params, [param]: (params[param] - 1 + modulus) % modulus })
  const increment = param => setParams({ ...params, [param]: (params[param] + 1) % modulus })

  return (
    <AppContext.Provider value={{
      modulus, setModulus, modInverse,
      generator, setGenerator,
      params, increment, decrement,
      scalars, graph, slopeAt, addPoints,
      check,
    }}>
      { children }
    </AppContext.Provider>
  )
}

AppContextProvider.propTypes = {
  children: PropTypes.node.isRequired,
}
