import { useMemo } from 'react'
import prime from 'get-primes'
import { InlineMath } from 'react-katex'
import 'katex/dist/katex.min.css'
import { useApp } from '../context'

const primes = prime(200)

export const ConfigForm = () => {
  const {
    modulus, setModulus,
    params, increment, decrement,
  } = useApp()

  const handleSelectModulus = event => {
    setModulus(+event.target.value)
  }

  const discriminant = useMemo(() => {
    return (4 * params.a**3 + 27 * params.b**2) & modulus
  }, [modulus, params.a, params.b])

  return (
    <div style={{ width: '800px', borderBottom: '1px solid lightgrey', marginBottom: '1rem' }}>
      <div style={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', color: 'grey', border: '1px dashed grey' }}>
        <InlineMath math="y^2 = x^3 + ax + b, \text{ over }\mathbb{Z} / p \mathbb{Z}" />
      </div>

      <div style={{
        display: 'flex',
        justifyContent: 'space-around',
      }}>
        <div style={{  display: 'flex', alignItems: 'center', justifyContent: 'center', gap: '0.5rem' }}>
          <InlineMath math="p = " />
          <select value={ modulus } onChange={ handleSelectModulus }>
            {
              primes.map(p => <option value={ p } key={ `option-${ p }` }>{ p }</option>)
            }
          </select>
        </div>

        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
          <InlineMath math={ `a = ${ params.a }` } />
          <span>
            <button style={{ width: '25px' }} onClick={ () => decrement('a') }>-</button>
            <button style={{ width: '25px' }} onClick={ () => increment('a') }>+</button>
          </span>
        </div>
        
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
          <InlineMath math={ `b = ${ params.b }` } />
          <span>
            <button style={{ width: '25px' }} onClick={ () => decrement('b') }>-</button>
            <button style={{ width: '25px' }} onClick={ () => increment('b') }>+</button>
          </span>
        </div>

        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', gap: '0.5rem' }}>
          <InlineMath math="\Longrightarrow" />
        </div>
        
        <div style={{ textAlign: 'center', padding: '1rem' }}>
          <InlineMath math={
            `y^2 = x^3 
            ${ params.a === 0 ? '' : `+ ${ params.a === 1 ? '' : params.a }x` }
            ${ params.b === 0 ? '' : `+ ${ params.b }` },\\newline
            \\text{ over }\\mathbb{Z} / ${ modulus } \\mathbb{Z}`
          } />
        </div>
      </div>

      {
        discriminant === 0 && (
          <div style={{ color: 'crimson', backgroundColor: '#f993', margin: '1rem 0', padding: '1rem', textAlign: 'center' }}>
            Must have <InlineMath math="4a^3 + 27b^2 \neq 0." />
          </div>
        )
      }
      
    </div>
  )
}
