import { Fragment, useMemo } from 'react'
import { useApp } from '../context'
import { InlineMath } from 'react-katex'

//

export const Details = () => {
  const { addPoints, generator, graph } = useApp()

  const subgroup = useMemo(() => {
    if (!generator) {
      return []
    }
    let elements = [generator]
    let prevPoint = generator
    let nextPoint = addPoints(prevPoint, generator)
    while (nextPoint && (nextPoint.x !== generator.x || nextPoint.y !== generator.y)) {
      elements = [...elements, nextPoint]
      prevPoint = elements.slice(-1)[0]
      nextPoint = addPoints(prevPoint, generator)
    }
    return elements
  }, [generator])

  return (
    <div style={{ width: '800px' }}>
      <div>
        <InlineMath math={ `\\text{There are } ${ graph.length + 1} \\text{ solutions.}` } />
      </div>

      {
        generator
          ? (
            <Fragment>
              <div>
                <InlineMath math={ `\\text{Generator } g = (${ generator.x }, ${ generator.y })` } />
              </div>
              <hr />
              <div>
                <InlineMath math={ `g \\text{ generates a subgroup of order } ${ subgroup.length }: ` } />{' '}
                <InlineMath math={ subgroup.map(g => `(${ g.x }, ${ g.y })`).join('\\mapsto') } />
              </div>
            </Fragment>
          ) : <InlineMath math={ `\\text{Generator } g = \\text{undefined}` } />
      }

    </div>
  )
}